import "./App.css";
import myphoto from "./assets/mainpagewoman.jpg";
import {
  Dropdown,
  DropdownTrigger,
  DropdownMenu,
  DropdownItem,
  Button,
} from "@nextui-org/react";

import { Link } from "react-router-dom";

const MainPage = () => {
  const redirectToLink = () => {
    window.open("https://kohaesthetics.janeapp.com", "_blank");
  };

  return (
    <>
      <div className="main-container">
        <header>
          <span className="logo">
            <Link to="/" className="item">
              Koh Aesthetics
            </Link>
            {/*<a href="#" className="item">
              About
            </a>*/}
            <Dropdown>
              <DropdownTrigger>
                <Button variant="bordered">Open Menu</Button>
              </DropdownTrigger>
              <DropdownMenu aria-label="Static Actions">
                <DropdownItem key="1">
                  <Link to="/" className="dropdown">
                    Injectables
                  </Link>
                </DropdownItem>
                <DropdownItem key="2">IV Infusion</DropdownItem>
                <DropdownItem key="3">PDO Threads</DropdownItem>
                <DropdownItem key="4">Weight Loss Program</DropdownItem>
                <DropdownItem key="5">Microneedling</DropdownItem>
                <DropdownItem key="6">Hair Restoration</DropdownItem>
              </DropdownMenu>
            </Dropdown>
          </span>
        </header>
        <main>
          {/*<div className="background" src=""></div> */}

          <div
            className="app-button"
            style={{
              backgroundColor: "#C49DBC",
              color: "#FFFFFF",
              padding: "20px",
              textAlign: "center",
              width: "100%",
              height: "300px",
              zIndex: "11",
              justifyContent: "center",
              alignItems: "center",
              marginTop: "30px",
            }}
          >
            <h1
              style={{
                fontSize: "20px",
                marginBottom: "10px",
                color: "#FFFFFF",
              }}
            >
              Welcome to Koh Aesthetics!
            </h1>
            <p
              style={{
                fontSize: "15px",
                marginBottom: "20px",
                color: "#FFFFFF",
              }}
            >
              We're here to enhance your natural beauty with personalized care.
              Dr. Koh is dedicated to providing individualized aesthetic
              solutions that highlight your unique features. Interested in a
              personalized consultation? Book an appointment today!
            </p>
            <Button
              onClick={redirectToLink}
              style={{
                backgroundColor: "#FFFDF9",
                color: "#323232",
                fontWeight: "500",
                width: "100%",
              }}
            >
              Book an Appointment
            </Button>
          </div>
          <div className="container">
            <div className="why">
              <h1>Why Us?</h1>
              <p
                style={{
                  fontSize: "15px",
                  marginBottom: "20px",
                  color: "#333333",
                }}
              >
                Choose Koh Aesthetics for a transformative med spa experience
                that seamlessly blends artistry with cutting-edge science.
                Founded by Dr. Koh, our commitment to your unique beauty sets us
                apart. With personalized treatments and a focus on the latest
                technologies, we deliver results that go beyond the ordinary.
                Trust us to bring out your timeless radiance - where expertise
                meets innovation effortlessly. Welcome to a world where your
                aesthetic aspirations become a stunning reality.
              </p>
            </div>
            <img src={myphoto} alt=" woman smiling " />
          </div>

          <div className="listing-container">
            <Link className="IJ">
              <div className="link-title">Injections</div>
            </Link>

            <Link className="IV">
              <div className="link-title">IV Vitamin Therapy</div>
            </Link>

            <Link className="PDO">
              <div className="link-title">PDO Threads</div>
            </Link>
          </div>

          <div className="listing-container">
            <Link className="HR">
              <div className="link-title"> Hair Restoration</div>
            </Link>

            <Link className="WLP">
              <div className="link-title">Weight Loss Program</div>
            </Link>
          </div>

          <div className="bottom">
            <div class="header">
              <h1>Koh Aesthetics</h1>
            </div>

            <div class="bottom-banner">
              <p>Email: kohaestheticsnyc@gmail.com</p>
              <p>Phone: (646)771-1199</p>
              <p>Location: 501 5th Ave, Ste. 1203, Manhattan NY 10017</p>
              <p>Opening Hours: Mon-Sat 10am-6pm</p>
            </div>
          </div>
        </main>
      </div>
    </>
  );
};

export default MainPage;
